@media screen and (min-width: 960px){
    
.PayBox {
    background: none !important;
    background-color: transparent !important; 
    padding-top:100px;
    padding-bottom:0px;
    width: 100%;
    xmax-width: 900px;
    margin: 0px auto;
    text-align: center;
}

.pay {
    background: none !important;
    background-color: transparent !important;
    padding-top:100px;
    padding-bottom:100px;
}

#pay h1 span{
font-weight: 300 !important;
line-height: 52px;
font-size: 48px;
font-family: 'Open Sans', sans-serif;
}

.separator {
     margin: 0px auto;
     text-align: center;
     width: 300px;
     height: 2px;
     background-color: #444444;
     border-bottom: 2px solid #444444 ;
     margin-bottom:24px;
    }
}


@media screen and (max-width: 960px){
    
    .PayBox {
        background: none !important;
        background-color: transparent !important;
        padding-top:100px;
        padding-bottom:0px;
    }
    
    .pay {
        background-color: #ffffff;
        padding-top:100px;
        padding-bottom:100px;
    }
    
    #pay h1 {
        font-size: 36px;
        line-height: 36px;
        font-weight: 300;
        -webkit-letter-spacing: 3px;
        -moz-letter-spacing: 3px;
        -ms-letter-spacing: 3px;
        letter-spacing: 3px;
        color: #444444;
        margin-bottom: 44px;
        font-family: 'Open Sans',sans-serif;
        text-align: center;
        }

    #pay h1 span{
    font-weight: 300 !important;
    line-height: 24px;
    font-size: 24px;
    font-family: 'Open Sans', sans-serif;
    }
    
    .separator {
         margin: 0px auto;
         text-align: center;
         width: 300px;
         height: 2px;
         background-color: #444444;
         border-bottom: 2px solid #444444 ;
         margin-bottom:24px;
        }
    }