@media (min-width: 960px) {

	.wrapper-section .slider {
		width: 100%;
		height: 100px;
		max-width: 1100px;
		overflow: hidden;
		position: relative;
		text-align: center;
		margin: 0px auto;
	}
	
	.wrapper-section .SlideLogos {
		width: 100%;
		min-height: 100px;
		text-align: center;
		margin: 0px auto;
	}
	
	.wrapper-section section {
		width: 100%;
		min-height: 100px !important;
		position: relative;
		text-align: center;
		margin: 0px auto;
	}
	
	.slider__title {
		position: absolute;
		width: 100%;
		text-align: center;
		top: 4rem;
		color: #fff !important;
		text-transform: uppercase;
		padding: 0 4rem;
		z-index: 100;
		text-shadow: 0 0 0.5rem rgb(9, 9, 34);
	}
	.slider__content,
	.slider__content--item.img {
		width: 100%;
		height: 100px;
		position: relative;
		text-align: center;
		margin: 0px auto;
	}
	
	.slider__content--item.img img {
		width: 200px;
		height: 100px;
		text-align: center;
		margin: 0px auto;
		
	}
	
	.slider-description {
		position: absolute;
		top: 34%;
		width: 100%;
		text-align: center;
		font-size: 2.5rem;
		font-weight: bold;
		padding: 1rem 4rem;
		background-color: rgba(255, 85, 85, 0.0);
		color: #f99d27 !important;
	}
	
	.slider-description1 {
