@media (min-width: 960px) {

.box-carrousel .slider {
	width: 100%;
	max-width: 1100px;
	height: 300px;
	overflow: auto !important;
	position: relative;
	margin: 0px auto !important;
	text-align: center !important;

}
.box-carrousel .slider__title {
	position: absolute;
	width: 100%;
	text-align: center;
	top: 4rem;
	color: #444444 !important;
	text-transform: uppercase;
	padding: 0 4rem;
	z-index: 100;
	text-shadow: 0 0 0.5rem rgb(9, 9, 34);
}
.box-carrousel .slider__content,
.box-carrousel .slider__content--item {
	width: 100%;
	height: 200px;
	position: relative;
}

.box-carrousel    {
	width: 100%;
	margin: 0px auto;
	text-align: center;
}

.box-carrousel .slider__content--item   {

	max-width: 800px;
	margin: 0px auto;
	text-align: center;
}

.box-carrousel .slider__content--item img {
	width: 100%;
	height: 100%;
	object-fit: cover;
	display: none;
}


.box-carrousel .slider-description {
	position: absolute;
	top: 75%;
	width: 100%;
	text-align: center;
	font-size: 24px;
	line-height: 26px;
	font-weight: bold;
	padding: 0px;
	background-color: rgba(255, 85, 85, 0.0);
	color: #f99d27 !important;
}

.box-carrousel .slider-description1 {
	position: absolute;
	top: 90%;
	width: 100%;
	text-align: center;
	font-size: 18px;
	line-height: 20px;
	font-weight: bold;
	padding: 0px;
	background-color: rgba(255, 85, 85, 0.0);
	color: #444444 !important;
}

.box-carrousel .slider-description2 {
	position: absolute;
	top: 30%;
	width: 100%;
	text-align: center;
	font-size: 14px;
	line-height: 16px;
	font-weight: 400;
	padding: 0px;
	background-color: rgba(255, 85, 85, 0.0);
	color: #444444 !important;
}

}

@media (max-width: 960px) {

	.box-carrousel .slider {
		width: 100%;
		max-width: 1100px;
		height: 300px;
		overflow: auto !important;
		position: relative;
		margin: 0px auto !important;
		text-align: center !important;
	
	}
	.box-carrousel .slider__title {
		position: absolute;
		width: 100%;
		text-align: center;
		top: 4rem;
		color: #444444 !important;
		text-transform: uppercase;
		padding: 0 4rem;
		z-index: 100;
		text-shadow: 0 0 0.5rem rgb(9, 9, 34);
	}
	.box-carrousel .slider__content,
	.box-carrousel .slider__content--item {
		width: 100%;
		height: 300px;
		position: relative;
	}
	
	.box-carrousel    {
		width: 100%;
		margin: 0px auto;
		height: 300px;
		overflow: hidden !important;
		text-align: center;
	}
	
	.box-carrousel .slider__content--item   {
	
		max-width: 350px;
		margin: 0px auto;
		text-align: center;
	}
	
	.box-carrousel .slider__content--item img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		display: none;
	}
	
	
	.box-carrousel .slider-description {
		position: absolute;
		top: 75%;
		width: 100%;
		text-align: center;
		font-size: 24px;
		line-height: 26px;
		font-weight: bold;
		padding: 0px;
		background-color: rgba(255, 85, 85, 0.0);
		color: #f99d27 !important;
	}
	
	.box-carrousel .slider-description1 {
		position: absolute;
		top: 86%;
		width: 100%;
		text-align: center;
		font-size: 18px;
		line-height: 20px;
		font-weight: bold;
		padding: 0px;
		background-color: rgba(255, 85, 85, 0.0);
		color: #444444 !important;
	}
	
	.box-carrousel .slider-description2 {
		position: absolute;
		top: 15%;
		width: 100%;
		text-align: center;
		font-size: 14px;
		line-height: 16px;
		font-weight: 400;
		padding: 0px;
		background-color: rgba(255, 85, 85, 0.0);
		color: #444444 !important;
	}
	
}

.box-carrousel .carousel-prev, .box-carrousel  .carousel-next {
    
    width: 40px;
    height: 100%;
	max-height: 200px;
    border-radius: 100%;
    position: absolute;
    top: 0;
}

.box-carrousel .carousel-prev:before {
    background-image: url("https://jlm-agency.com/coin/images/arrow3.png");
}

.carousel-prev:before {
    background-image: url("https://jlm-agency.com/coin/images/arrow2.png");
}

.box-carrousel .carousel-next:before {
    background-image: url("https://jlm-agency.com/coin/images/arrow4.png");
}

.carousel-next:before {
    background-image: url("https://jlm-agency.com/coin/images/arrow1.png");
}