
 
    
    .navbar {
  background: none;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: relative !important;
  z-index: 100 !important;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
}

.fa-firstdraft {
  margin-left: 0.5rem;
  font-size: 14px;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(7, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  display: flex;
  align-items: center;
  height: 80px;
  font-size: 14px;
}

.nav-links {
  color:#222222;
  text-decoration: none;
  padding: 0.5rem 1rem;
  font-weight: 300;
  letter-spacing: 1px;
  font-size: 16px;
}

.nav-links:hover {
  color: #f99d27;
  border-radius: 4px;
  transition: all 0.2s ease-out;
  
}


.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (min-width: 960px) {
  
.acceso-item { 
  border: 2px solid #f99d27;
  background-color: #f99d27;
  color: #ffffff;
  padding: 15px 35px;
  border-radius: 0px;

}

.acceso-item:hover { 
  border: 2px solid #444444;
  border-radius: 0px;
  background-color: #444444;
  color: #ffffff;

}

}



@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
    color: #fff;
  }

  .nav-links:hover {
    background-color: #f99d27;
    border-radius: 0;
    color:#222222;
    
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0px;
    right: 0px;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    z-index: 99999 !important;
    height: 50px;
    width: 50px;
    color: #444444;
    
  }

  .fa-times {
    color: #444444;
    font-size: 2rem;
  }

  .fa-bars {
    color: #444444;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: #f99d27;
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #f99d27;
    transition: 250ms;
  }

  button {
    xdisplay: none;
  }

  .acceso-item { 
    border: 2px solid #f99d27;
    background-color: #f99d27;
    color: #ffffff;
    padding: 15px 35px;
    border-radius: 35px;
    width: 300px;
    margin: 0px auto;
  
  }
  
  .acceso-item:hover { 
    border: 2px solid #ffffff;
    border-radius: 35px;
    background-color: #ffffff;
    color: #444444;
  
  }
}

.home-page { display: none;}

