@media (min-width: 960px) {

.slideshow-box .slider {
	width: 100% !important;
	height: 400px !important;
	overflow: hidden;
	position: relative;
}
.slider__title {
	position: absolute;
	width: 100%;
	text-align: center;
	top: 4rem;
	color: #fff !important;
	text-transform: uppercase;
	padding: 0 4rem;
	z-index: 100;
	text-shadow: 0 0 0.5rem rgb(9, 9, 34);
}
.slideshow-box .slider__content,
.slideshow-box .slider__content--item {
	width: 100% !important;
	height: 400px !important;
	position: relative;
}

.slideshow-box .slider__content--item img {
	width: 100% !important;
	height: 100% !important;
	object-fit: cover;
}

.slider-description {
	position: absolute;
	top: 34%;
	width: 100%;
	text-align: center;
	font-size: 2.5rem;
	font-weight: bold;
	padding: 1rem 4rem;
	background-color: rgba(255, 85, 85, 0.0);
	color: #f99d27 !important;
}

.slider-description1 {
	position: absolute;
	top: 45%;
	width: 100%;
	text-align: center;
	font-size: 2.5rem;
	font-weight: bold;
	padding: 1rem 4rem;
	background-color: rgba(255, 85, 85, 0.0);
	color: #fff !important;
}

.slider-description2 {
	position: absolute;
	top: 55%;
	width: 100%;
	text-align: center;
	font-size: 2.5rem;
	font-weight: bold;
	padding: 1rem 4rem;
	background-color: rgba(255, 85, 85, 0.0);
	color: #fff !important;
}

.slider-description3 {
	position: absolute;
	top: 60%;
	width: 100%;
	height: 100px;
	text-align: center;
	font-size: 2.5rem;
	font-weight: bold;
	padding: 1rem 4rem;
	background-image: url("https://jlm-agency.com/coin/images/logos-pay.png");
	background-repeat: no-repeat;
	background-position: center;
	color: #fff !important;
}

#1 .slider-description3 {
	display: none;
}

}

@media (max-width: 960px) {

	.slideshow-box .slider {
		width: 100%;
		height: 400px;
		overflow: hidden;
		position: relative;
	}
	.slideshow-box .slider__title {
		position: absolute;
		width: 100%;
		text-align: center;
		top: 4rem;
		color: #fff !important;
		text-transform: uppercase;
		padding: 0 4rem;
		z-index: 100;
		text-shadow: 0 0 0.5rem rgb(9, 9, 34);
	}
	.slideshow-box .slider__content,
	.slideshow-box .slider__content--item {
		width: 100%;
		height: 400px;
		position: relative;
	}
	
	.slideshow-box .slider__content--item img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	
	.slider-description {
		position: absolute;
		top: 37%;
		width: 100%;
		text-align: center;
		font-size: 24px;
		line-height: 26px;
		font-weight: bold;
		padding: 1rem 4rem;
		background-color: rgba(255, 85, 85, 0.0);
		color: #f99d27 !important;
	}
	
	.slider-description1 {
		position: absolute;
		top: 45%;
		width: 100%;
		text-align: center;
		font-size: 24px;
		line-height: 26px;
		font-weight: bold;
		padding: 1rem 4rem;
		background-color: rgba(255, 85, 85, 0.0);
		color: #fff !important;
	}
	
}